<template>
    <v-layout>
        <v-flex xs12 sm4 offset-sm4>
          <v-alert
            :value="isEmailSent"
            type="success"
          >
            The email is sent.
          </v-alert>
          <v-card>
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="contact-form pa-3"
            >
              <v-text-field
                v-model="title"
                :counter="10"
                :rules="titleRules"
                label="Title"
                required
              ></v-text-field>

              <v-text-field
                v-model="email"
                :rules="emailRules"
                label="E-mail"
                required
              ></v-text-field>

              <v-textarea
                v-model="text"
                :rules="[v => !!v || 'Text is required.']"
                label="Text"
                required
              ></v-textarea>

              <v-checkbox
                v-model="checkbox"
                :rules="[v => !!v || 'You must agree to continue.']"
                label="I am not a bot."
                required
              ></v-checkbox>

              <v-btn
                :disabled="!valid"
                color="success"
                @click="validate"
              >
                Send
              </v-btn>
            </v-form>
          </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import db from './../firestoreInit';
import { setTimeout } from 'timers';

export default {
  name: 'Contacts',
  data() {
    return {
      valid: true,
      title: '',
      titleRules: [
          v => !!v || 'Title is required',
          v => (v && v.length <= 10) || 'Title must be less than 10 characters.'
      ],
      email: '',
      emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid.'
      ],
      text: '',
      checkbox: false,
      isEmailSent: false
    }
  },
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        db
          .collection('emails')
          .add({
            title: this.title,
            email: this.email,
            text: this.text
          })
          .then(doc => {
              this.$refs.form.reset();
              this.isEmailSent = true;
              setTimeout(() => {
                this.isEmailSent = false
              }, 3000);
          });
      }
    }
  }
}
</script>

